
.biaodan {
  z-index: 10;
  position: relative;
  padding-bottom: 50px;
}
.biaodan > .content {
  width: 1000px;
  background: #fff;
  padding: 60px 60px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: -200px auto 60px;
}
.biaodan > .content > .back {
  color: #bbb;
}
.biaodan > .content > .back:hover {
  color: #313131;
}
.biaodan > .content > p {
  color: #727272;
  font-size: 18px;
  margin-top: 40px;
  line-height: 24px;
}
.desc {
  font-size: 18px;
  margin-top: 20px;
  line-height: 1.5;
}
.desc > p {
  word-wrap: break-word;
  margin: 0 0 15px 0;
  color: #333;
}
.biaodan > .content > p:nth-of-type(1) {
  color: #d01667;
  font-size: 30px;
}
.biaodan > .content > p:nth-of-type(2) {
  color: #313131;
  font-size: 26px;
  margin: 40px 0;
  text-align: center;
  border-bottom: 1px solid #e0e0e0;
  line-height: 90px;
}
.biaodan > .content > ul {
  list-style: none;
  overflow: hidden;
  margin-top: 30px;
}
.biaodan > .content > ul > li {
  float: left;
  margin-right: 10px;
  width: 210px;
  height: 210px;
  text-align: center;
  border: 1px solid #eee;
  overflow: hidden;
  cursor: pointer;
}
.li_img {
  opacity: 0.7;
}
.li_img:hover {
  opacity: 1;
}
.biaodan > .content > ul > li > img {
}
.biaodan > .content > .img {
  background: url("http://image.houxiaopang.com/demand/baojia_nml.png");
  background-size: cover;
  width: 200px;
  height: 52px;
  margin: 90px auto 30px auto;
  cursor: pointer;
}
.biaodan > .content > .active {
  width: 200px;
  height: 52px;
  margin: 90px auto 30px auto;
  cursor: pointer;
  background: url("http://image.houxiaopang.com/demand/baojia_ovr.png");
  background-size: cover;
}
.biaodan > .content > .sendimg {
  background: url("http://image.houxiaopang.com/demand/send_nml.png");
  background-size: cover;
  width: 200px;
  height: 52px;
  margin: 90px auto 30px auto;
  cursor: pointer;
}
.biaodan > .content > .sendimg:hover {
  background: url("http://image.houxiaopang.com/demand/send_ovr.png");
  background-size: cover;
}
.content > .input-group {
  margin-bottom: 50px;
  overflow: hidden;
}
.content > .input-group > label {
  color: #d01667;
  display: inline-block;
  font-size: 24px;
  line-height: 45px;
}
.content > .input-group > input {
  outline: none;
  border: 1px solid #999;
  height: 45px;
  font-size: 20px;
  padding-left: 10px;
}
::-webkit-input-placeholder {
  color: #bfbfbf;
}
.content > .input-group > textarea {
  width: 99%;
  border: 1px solid #999;
  outline: none;
  font-size: 20px;
  padding-left: 10px;
  font-family: "Helvetica Neue", Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "HeitiSC", "WenQuanYi Micro Hei", sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 300px;
  margin-top: 20px;
}
.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, .5);
  z-index: 100;
}
.modal > .login {
  width: 403px;
  height: 433px;
  margin: 100px auto;
  background: #fff;
  position: relative;
  padding: 15px 0;
}
#wxqrcode {
  width: 302px;
  margin: 0 auto;
}
.prompt {
  margin-top: 60px !important;
  height: 395px;
  background: url('http://image.houxiaopang.com/demand/prompt.png') no-repeat !important;
  background-size: 100% !important;
}
.prompt > .prompt_help {
  font-size: 20px;
  line-height: 50px;
  background: -webkit-gradient(linear, left top, right top, from(#fe6549), to(#d01667));
  background: linear-gradient(to right, #fe6549, #d01667);
  width: 500px;
  margin: 63px auto;
  text-align: center;
  border-radius: 5px;
  color: #fff;
}



